.home {
    position: relative;
    width: 100%;
    height: 100vh;

    .name-container {
        position: absolute;
        bottom: 150px;
        left: 100px;

        h1, h2 {
            color: white;
            
            .name {
                color: red;
            }
        }

        h1 {
            font-size: 4rem;
        }

        h2 {
            font-size: 3rem;
        }
    }

    .avatar {
        width: 300px;
        position: relative;
        // float: right;
        left: 100%;
        top: 40%;
        transform: translate(-150%, -40%);
        border-radius: 50%;
        box-shadow: 0px 0px 15px 3px black ;
    }
}